import { NextResponse } from 'next/server';
import { writeFile } from 'fs/promises';
import path from 'path';
import mysql from 'mysql2/promise';

export async function POST(req) {
    try {
        const formData = await req.formData();
        const file = formData.get('file');
        const projectId = formData.get('projectId');
        const uploadedBy = formData.get('uploaded_by');

        if (!file) {
            return NextResponse.json({ error: "No file uploaded" }, { status: 400 });
        }

        // 1. จัดการบันทึกไฟล์ลงโฟลเดอร์ public/uploads
        const bytes = await file.arrayBuffer();
        const buffer = Buffer.from(bytes);
        const filename = `${Date.now()}-${file.name}`;
        const filePath = path.join(process.cwd(), 'public/uploads', filename);
        await writeFile(filePath, buffer);

        // 2. บันทึกข้อมูลลงฐานข้อมูล
        const connection = await mysql.createConnection({
            host: process.env.DB_HOST,
            user: process.env.DB_USER,
            password: process.env.DB_PASSWORD,
            database: process.env.DB_NAME,
        });

        await connection.execute(
            'INSERT INTO project_documents (project_id, file_name, file_path, file_type, uploaded_by) VALUES (?, ?, ?, ?, ?)',
            [projectId, file.name, `/uploads/${filename}`, file.type, uploadedBy]
        );

        await connection.end();

        return NextResponse.json({ message: "Upload success" });
    } catch (error) {
        console.error("Upload Error:", error);
        return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
    }
}